<template>
  <div
    :style="{
      width: '1000px',
      padding: '20px',
      boxShadow: '0px 4px 10px 0px rgba(0,0,0,0.302)',
      margin: '10px auto',
      position: 'relative',
      background: '#fff',
    }"
  >
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
    >
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="yuyuedanhao"
      >
        <el-input
          v-model="ruleForm.yuyuedanhao"
          placeholder=""
          readonly
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="mingcheng"
      >
        <el-input
          v-model="ruleForm.mingcheng"
          placeholder=""
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        v-if="type != 'cross' || (type == 'cross' && !ro.tupian)"
        prop="tupian"
      >
        <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.tupian ? ruleForm.tupian : ''"
          @change="tupianUploadChange"
        ></file-upload>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        class="upload"
        v-else
        label=""
        prop="tupian"
      >
        <img
          v-if="ruleForm.tupian.substring(0, 4) == 'http'"
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          :src="ruleForm.tupian.split(',')[0]"
          width="100"
          height="100"
        />
        <img
          v-else
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          v-for="(item, index) in ruleForm.tupian.split(',')"
          :src="baseUrl + item"
          width="100"
          height="100"
        />
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="zuowei"
      >
        <el-input
          v-model="ruleForm.zuowei"
          placeholder=""
          clearable
          readonly
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="qiandaozhuangtai"
      >
        <el-select
          v-model="ruleForm.qiandaozhuangtai"
          placeholder=""
          disabled
        >
          <el-option
            v-for="(item, index) in qiandaozhuangtaiOptions"
            :key="index"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="qiantuizhuangtai"
      >
        <el-select
          v-model="ruleForm.qiantuizhuangtai"
          placeholder=""
          disabled
        >
          <el-option
            v-for="(item, index) in qiantuizhuangtaiOptions"
            :key="index"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="yuyueshijian"
      >
        <el-date-picker
          value-format="yyyy-MM-dd HH:mm:ss"
          v-model="ruleForm.yuyueshijian"
          type="datetime"
          placeholder=""
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="beizhu"
      >
        <el-input
          v-model="ruleForm.beizhu"
          placeholder=""
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="xuehao"
      >
        <el-input
          v-model="ruleForm.xuehao"
          placeholder=""
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="xingming"
      >
        <el-input
          v-model="ruleForm.xingming"
          placeholder=""
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{
          width: '80%',
          padding: '10px',
          margin: '0 2% 10px',
          display: 'inline-block',
        }"
        label=""
        prop="shouji"
      >
        <el-input
          v-model="ruleForm.shouji"
          placeholder=""
          clearable
        ></el-input>
      </el-form-item>

      <el-form-item :style="{ padding: '0', margin: '0' }">
        <el-button
          :style="{
            border: '0',
            cursor: 'pointer',
            padding: '0',
            margin: '0 20px 0 0',
            backgroundColor: '#fff',
            color: 'rgba(255, 255, 255, 1)',
            backgroundImage:
              'url(http://codegen.caihongy.cn/20221018/f6c1f39bff574b6cb2564bbedb76b604.png)',
            outline: 'none',
            borderRadius: '4px',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            backgroundSize: '100% 100%',
            height: '40px',
          }"
          type="primary"
          @click="onSubmit"
          ></el-button
        >
        <el-button
          :style="{
            border: '0px solid rgba(64, 158, 255, 1)',
            cursor: 'pointer',
            padding: '0',
            margin: '0',
            backgroundColor: '#fff',
            color: '#fff',
            backgroundImage:
              'url(http://codegen.caihongy.cn/20221018/71c4fd01e19542dda750b9c9523c11bd.png)',
            outline: 'none',
            borderRadius: '4px',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            backgroundSize: '100% 100%',
            height: '40px',
          }"
          @click="back()"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      baseUrl: "",
      ro: {
        zixishiid: false,
        yuyuedanhao: false,
        mingcheng: false,
        tupian: false,
        zuowei: false,
        qiandaozhuangtai: false,
        qiantuizhuangtai: false,
        yuyueshijian: false,
        beizhu: false,
        xuehao: false,
        xingming: false,
        shouji: false,
        sfsh: false,
        shhf: false,
      },
      type: "",
      userTableName: localStorage.getItem("UserTableName"),
      ruleForm: {
        zixishiid:"",
        yuyuedanhao: this.getUUID(),
        mingcheng: "",
        tupian: "",
        zuowei: "",
        qiandaozhuangtai: "",
        qiantuizhuangtai: "",
        yuyueshijian: "",
        beizhu: "",
        xuehao: "",
        xingming: "",
        shouji: "",
      },
      qiandaozhuangtaiOptions: [],
      qiantuizhuangtaiOptions: [],
      rules: {
        yuyuedanhao: [],
        mingcheng: [],
        tupian: [],
        zuowei: [
          // { validator: this.$validate.isIntNumer, trigger: 'blur' },
        ],
        qiandaozhuangtai: [],
        qiantuizhuangtai: [],
        yuyueshijian: [
          { required: true, message: "", trigger: "blur" },
        ],
        beizhu: [],
        xuehao: [],
        xingming: [],
        shouji: [{ validator: this.$validate.isMobile, trigger: "blur" }],
        sfsh: [],
        shhf: [],
      },
    };
  },
  computed: {},
  created() {
    //this.bg();
    let type = this.$route.query.type ? this.$route.query.type : "";
    this.init(type);
    this.baseUrl = this.$config.baseUrl;
  },
  methods: {
    getMakeZero(s) {
      return s < 10 ? "0" + s : s;
    },
    // 
    download(file) {
      window.open(`${file}`);
    },
    // 
    init(type) {
      this.type = type;
      if (type == "cross") {
        var obj = JSON.parse(localStorage.getItem("crossObj"));
        // console.log(obj);
        for (var o in obj) {
          if (o == 'id'){
            this.ruleForm.zixishiid = obj[o];
            this.ro.zixishiid = true;
            // console.log(obj[o]);
            continue;
          }
          if (o == "yuyuedanhao") {
            this.ruleForm.yuyuedanhao = obj[o];
            this.ro.yuyuedanhao = true;
            continue;
          }
          if (o == "mingcheng") {
            this.ruleForm.mingcheng = obj[o];
            this.ro.mingcheng = true;
            continue;
          }
          if (o == "tupian") {
            this.ruleForm.tupian = obj[o].split(",")[0];
            this.ro.tupian = true;
            continue;
          }
          if (o == "zuowei") {
            // console.log(obj[o]);
            this.ruleForm.zuowei = obj[o];
            this.ro.zuowei = true;
            continue;
          }
          if (o == "qiandaozhuangtai") {
            this.ruleForm.qiandaozhuangtai = obj[o];
            this.ro.qiandaozhuangtai = true;
            continue;
          }
          if (o == "qiantuizhuangtai") {
            this.ruleForm.qiantuizhuangtai = obj[o];
            this.ro.qiantuizhuangtai = true;
            continue;
          }
          if (o == "yuyueshijian") {
            this.ruleForm.yuyueshijian = obj[o];
            this.ro.yuyueshijian = true;
            continue;
          }
          if (o == "beizhu") {
            this.ruleForm.beizhu = obj[o];
            this.ro.beizhu = true;
            continue;
          }
          if (o == "xuehao") {
            this.ruleForm.xuehao = obj[o];
            this.ro.xuehao = true;
            continue;
          }
          if (o == "xingming") {
            this.ruleForm.xingming = obj[o];
            this.ro.xingming = true;
            continue;
          }
          if (o == "shouji") {
            this.ruleForm.shouji = obj[o];
            this.ro.shouji = true;
            continue;
          }
        }
        // this.ruleForm.zuowei = 0
      }
      // 
      this.$http
        .get(this.userTableName + "/session", { emulateJSON: true })
        .then((res) => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if ((json.xuehao != "" && json.xuehao) || json.xuehao == 0) {
              this.ruleForm.xuehao = json.xuehao;
            }
            if ((json.xingming != "" && json.xingming) || json.xingming == 0) {
              this.ruleForm.xingming = json.xingming;
            }
            if ((json.shouji != "" && json.shouji) || json.shouji == 0) {
              this.ruleForm.shouji = json.shouji;
            }
          }
        });
      this.qiandaozhuangtaiOptions = ",".split(",");
      this.qiantuizhuangtaiOptions = ",".split(",");
    },

    // 
    // 
    info(id) {
      this.$http
        .get("yuyuexinxi/detail/${id}", { emulateJSON: true })
        .then((res) => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
    },
    // 
    onSubmit() {
      var obj = JSON.parse(localStorage.getItem("crossObj"));
      var table = localStorage.getItem("crossTable");
      // obj.zuowei = obj.zuowei - this.ruleForm.zuowei
      // if(obj.zuowei<0){
      //   this.$message.error("");
      //   return
      // }

      //this.$http.post(table+`/update`, obj).then(res => {});
      //
      var crossuserid;
      var crossrefid;
      var crossoptnum;
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          if (this.type == "cross") {
            var statusColumnName = localStorage.getItem("statusColumnName");
            var statusColumnValue = localStorage.getItem("statusColumnValue");
            if (statusColumnName && statusColumnName != "") {
              var obj = JSON.parse(localStorage.getItem("crossObj"));
              //  console.log(obj);
              if (!statusColumnName.startsWith("[")) {
                for (var o in obj) {
                  if (o == statusColumnName) {
                    obj[o] = statusColumnValue;
                    //  console.log(obj[o]);
                  }
                }
                var table = localStorage.getItem("crossTable");
                //  console.log(table)
                //  this.$http.post(table+'/update', obj).then(res => {});
              } else {
                crossuserid = Number(localStorage.getItem("userid"));
                crossrefid = obj["id"];
                crossoptnum = localStorage.getItem("statusColumnName");
                crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
              }
            }
          }
          if (crossrefid && crossuserid) {
            this.ruleForm.crossuserid = crossuserid;
            this.ruleForm.crossrefid = crossrefid;
            var params = {
              page: 1,
              limit: 10,
              crossuserid: crossuserid,
              crossrefid: crossrefid,
            };
            this.$http
              .get("yuyuexinxi/list", {
                params: params,
              })
              .then((res) => {
                if (res.data.data.total >= crossoptnum) {
                  this.$message({
                    message: localStorage.getItem("tips"),
                    type: "success",
                    duration: 1500,
                  });
                  return false;
                } else {
                  // 
                  var obj = JSON.parse(localStorage.getItem("crossObj"));
                  var table = localStorage.getItem("crossTable");

                  // obj.zuowei = parseFloat(obj.zuowei) - parseFloat(this.ruleForm.zuowei)
                  // console.log('ss')
                  console.log(obj);
                  // this.$http.post(table+`/update`,obj).then(res => {});
                  this.$http
                    .post("yuyuexinxi/add", this.ruleForm)
                    .then((res) => {
                      if (res.data.code == 0) {
                        this.$message({
                          message: "",
                          type: "success",
                          duration: 1500,
                          onClose: () => {
                            this.$router.go(-1);
                          },
                        });
                      } else {
                        this.$message({
                          message: res.data.msg,
                          type: "error",
                          duration: 1500,
                        });
                      }
                    });
                }
              });
          } else {
            var obj = JSON.parse(localStorage.getItem("crossObj"));
            var table = localStorage.getItem("crossTable");

            // obj.zuowei = parseFloat(obj.zuowei) - parseFloat(this.ruleForm.zuowei)
            // console.log('sssss' + table)
            // console.log(obj);
            // this.$http.post(table+`/update`,obj).then(res => {});
            this.$http.post("yuyuexinxi/add", this.ruleForm).then((res) => {
              if (res.data.code == 0) {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 1500,
                  onClose: () => {
                    this.$router.go(-1);
                  },
                });
              } else {
                this.$message({
                  message: res.data.msg,
                  type: "error",
                  duration: 1500,
                });
              }
            });
          }
        }
      });
    },
    // uuid
    getUUID() {
      return new Date().getTime();
    },
    // 
    back() {
      this.$router.go(-1);
    },
    tupianUploadChange(fileUrls) {
      this.ruleForm.tupian = fileUrls.replace(
        new RegExp(this.$config.baseUrl, "g"),
        ""
      );
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.el-date-editor.el-input {
  width: auto;
}

.add-update-preview .el-form-item >>> .el-form-item__label {
  padding: 0 10px 0 0;
  color: #666;
  font-weight: 500;
  width: 120px;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
}

.add-update-preview .el-form-item >>> .el-form-item__content {
  margin-left: 120px;
}

.add-update-preview .el-input >>> .el-input__inner {
  border: 2px solid #2e61e1;
  border-radius: 4px;
  padding: 0 12px;
  outline: none;
  color: #000;
  background: #bfd2fc;
  width: 100%;
  font-size: 14px;
  height: 40px;
}

.add-update-preview .el-select >>> .el-input__inner {
  border: 2px solid #2e61e1;
  border-radius: 4px;
  padding: 0 10px;
  outline: none;
  color: #000;
  background: #bfd2fc;
  width: 100%;
  font-size: 14px;
  height: 40px;
}

.add-update-preview .el-date-editor >>> .el-input__inner {
  border: 2px solid #2e61e1;
  border-radius: 4px;
  padding: 0 10px 0 30px;
  outline: none;
  color: #000;
  background: #bfd2fc;
  width: 100%;
  font-size: 14px;
  height: 40px;
}

.add-update-preview >>> .el-upload--picture-card {
  background: transparent;
  border: 0;
  border-radius: 0;
  width: auto;
  height: auto;
  line-height: initial;
  vertical-align: middle;
}

.add-update-preview >>> .upload .upload-img {
  border: 1px dashed rgba(64, 158, 255, 1);
  cursor: pointer;
  border-radius: 6px;
  color: rgba(64, 158, 255, 1);
  width: 100px;
  font-size: 32px;
  line-height: 100px;
  text-align: center;
  height: 100px;
}

.add-update-preview >>> .el-upload-list .el-upload-list__item {
  border: 1px dashed rgba(64, 158, 255, 1);
  cursor: pointer;
  border-radius: 6px;
  color: rgba(64, 158, 255, 1);
  width: 100px;
  font-size: 32px;
  line-height: 100px;
  text-align: center;
  height: 100px;
}

.add-update-preview >>> .el-upload .el-icon-plus {
  border: 1px dashed rgba(64, 158, 255, 1);
  cursor: pointer;
  border-radius: 6px;
  color: rgba(64, 158, 255, 1);
  width: 100px;
  font-size: 32px;
  line-height: 100px;
  text-align: center;
  height: 100px;
}

.add-update-preview .el-textarea >>> .el-textarea__inner {
  border: 2px solid #2e61e1;
  border-radius: 4px;
  padding: 12px;
  outline: none;
  color: #000;
  background: #bfd2fc;
  width: 100%;
  font-size: 14px;
  height: 120px;
}
</style>
