<template>
<div class="center-preview" :style='{"width":"1000px","margin":"10px auto","position":"relative","background":"#fff"}'>
	<div class="title" :style='{"margin":"0px 0","color":"#333","textAlign":"center","background":"rgba(0,0,0,0.102)","fontSize":"20px","lineHeight":"54px","fontWeight":"600"}'>{{ title }}</div>

    <el-tabs tab-position="left" :style='{"boxShadow":"0px 4px 10px 0px rgba(0,0,0,0.302)","background":"#fff"}' @tab-click="handleClick">
      <el-tab-pane label="">
        <el-form class="center-preview-pv" ref="sessionForm" :model="sessionForm" :rules="rules" label-width="110px">
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="" prop="xuehao">
            <el-input v-model="sessionForm.xuehao" placeholder="" readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="" prop="mima">
            <el-input type="password" v-model="sessionForm.mima" placeholder=""></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="" prop="xingming">
            <el-input v-model="sessionForm.xingming" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="">
            <el-select v-model="sessionForm.xingbie" placeholder="" >
              <el-option v-for="(item, index) in dynamicProp.xingbie" :key="index" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="headers"
              :show-file-list="false"
              :on-success="xueshengtouxiangHandleAvatarSuccess">
              <img :style='{"border":"1px dashed #BFD2FC","cursor":"pointer","color":"#BFD2FC","borderRadius":"6px","textAlign":"center","width":"100px","fontSize":"32px","lineHeight":"100px","height":"100px"}' v-if="sessionForm.touxiang" :src="baseUrl + sessionForm.touxiang" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="" prop="shouji">
            <el-input v-model="sessionForm.shouji" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"45%","padding":"10px","margin":"0 2% 10px","background":"#fff","display":"inline-block"}' v-if="userTableName=='xuesheng'" label="" prop="banji">
            <el-input v-model="sessionForm.banji" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"0","margin":"20px 0 0"}'>
            <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"4px","background":"url(http://codegen.caihongy.cn/20221018/f6c1f39bff574b6cb2564bbedb76b604.png) no-repeat","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="primary" @click="onSubmit('sessionForm')"></el-button>
            <el-button :style='{"border":"0px solid rgba(64, 158, 255, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"4px","background":"url(http://codegen.caihongy.cn/20221018/71c4fd01e19542dda750b9c9523c11bd.png) no-repeat","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="danger" @click="logout"></el-button>
          </el-form-item>
        </el-form>
		
        
      </el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
    </el-tabs>
</div>
</template>

<script>
  import config from '@/config/config'
  import Vue from 'vue'
  export default {
    //
    data() {
      return {
        title: '',
        baseUrl: config.baseUrl,
        sessionForm: {},
        rules: {},
        chongzhiForm: {
          money: '',
          radio: ''
        },
        disabled: false,
        dialogFormVisibleMoney: false,
        dialogFormVisibleVip: false,
        uploadUrl: config.baseUrl + 'file/upload',
        imageUrl: '',
        headers: {Token: localStorage.getItem('Token')},
        userTableName: localStorage.getItem('UserTableName'),
        dynamicProp: {}
      }
    },
    created() {
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'xuehao', null);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'mima', null);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'xingming', null);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'xingbie', null);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'touxiang', null);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'shouji', null);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.sessionForm, 'banji', null);
      }

      if ('xuesheng' == this.userTableName) {
        this.$set(this.rules, 'xuehao', [{ required: true, message: '', trigger: 'blur' }]);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.rules, 'mima', [{ required: true, message: '', trigger: 'blur' }]);
      }
      if ('xuesheng' == this.userTableName) {
        this.$set(this.rules, 'xingming', [{ required: true, message: '', trigger: 'blur' }]);
      }
			if ('xuesheng' == this.userTableName) {
        this.$set(this.rules, 'shouji', [{ required: false, validator: this.$validate.isMobile, trigger: 'blur' }]);
      }

      this.init();
      this.getSession();
    },
    //
    methods: {
      init() {
        if ('xuesheng' == this.userTableName) {
          this.dynamicProp.xingbie = ','.split(',');
        }
      },
      getSession() {
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.sessionForm = res.data.data;
            localStorage.setItem('userid', res.data.data.id);
            if(res.data.data.vip) {
                localStorage.setItem('vip', res.data.data.vip);
            }
            if(res.data.data.touxiang) {
                localStorage.setItem('headportrait', res.data.data.touxiang);
            } else if(res.data.data.headportrait) {
                localStorage.setItem('headportrait', res.data.data.headportrait);
            }
          }
        });
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.post(this.userTableName + '/update', this.sessionForm).then(res => {
              if (res.data.code == 0) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 1500
                });
              }
            });
          } else {
            return false;
          }
        });
      },
      xueshengtouxiangHandleAvatarSuccess(res, file) {
        if (res.code == 0) {
          if ('xuesheng' == this.userTableName) {
            this.sessionForm.touxiang = 'upload/' + res.file;
          }
        }
      },
      chongzhi() {
        if (this.chongzhiForm.money == '') {
          this.$message({
            message: '',
            type: 'error',
            duration: 1500
          });
          return;
        }
        if (this.chongzhiForm.money <= 0) {
          this.$message({
            message: '',
            type: 'error',
            duration: 1500
          });
          return;
        }
        if (this.chongzhiForm.radio == '') {
          this.$message({
            message: '',
            type: 'error',
            duration: 1500
          });
          return;
        }
        this.sessionForm.money = parseInt(this.sessionForm.money) + parseInt(this.chongzhiForm.money);
        this.$http.post(this.userTableName + '/update', this.sessionForm).then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: '',
              type: 'success',
              duration: 1500,
              onClose: () => {
                this.dialogFormVisibleMoney = false;
              }
            });
          }
        });
      },
      chongzhivip() {
        this.chongzhiForm.money == 199;
        if (this.chongzhiForm.radio == '') {
          this.$message({
            message: '',
            type: 'error',
            duration: 1500
          });
          return;
        }
        if(this.sessionForm.vip == '') {
          this.$message({
            message: '',
            type: 'success',
            duration: 1500
          });
          return;
        }
        
        this.sessionForm.vip = ""
        this.$http.post(this.userTableName + '/update', this.sessionForm).then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: '',
              type: 'success',
              duration: 1500,
              onClose: () => {
                localStorage.setItem('vip', this.sessionForm.vip);
                this.dialogFormVisibleVip = false;
              }
            });
          }
        });
      },
      handleClick(tab, event) {
        switch(event.target.outerText) {
          case '':
            tab.$router.push('/index/center');
            break;
          case '':
            localStorage.setItem('storeupType', 1);
            tab.$router.push('/index/storeup');
            break;
        }

        this.title = event.target.outerText;
      },
      logout() {
        localStorage.clear();
        Vue.http.headers.common['Token'] = "";
        this.$router.push('/index/home');
        this.activeIndex = '0'
        localStorage.setItem('keyPath', this.activeIndex)
        this.$forceUpdate()
        this.$message({
            message: '',
            type: 'success',
            duration: 1500,
        });
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .center-preview {
  
    .el-tabs {
      & >>> .el-tabs__header {
        .el-tabs__nav-wrap {
          margin: 0;
  
          &::after {
            content: none;
          }
        }
  
        .el-tabs__active-bar {
          display: none !important;
        }
      }
  
      .center-preview-pv {
        .el-date-editor.el-input {
          width: auto;
        }
  
        .balance {
          .el-input {
            width: auto;
          }
        }
      }
    }
  }
  
  .center-preview .el-tabs >>> .el-tabs__header {
	padding: 20px;
	margin: 0;
	background: #BFD2FC;
	width: 180px;
	min-height: 600px;
	border-color: #eee;
	border-width: 0 1px 0 0;
	position: relative;
	float: left;
	border-style: solid;
  }
  
  .center-preview .el-tabs >>> .el-tabs__header .el-tabs__item {
  	padding: 0 10px;
  	margin: 10px 0;
  	color: #fff;
  	background: url(http://codegen.caihongy.cn/20221018/71c4fd01e19542dda750b9c9523c11bd.png) no-repeat center top;
  	font-weight: 500;
  	font-size: 14px;
  	line-height: 40px;
  	position: relative;
  	text-align: center;
  	height: 40px;
  }
  
  .center-preview .el-tabs >>> .el-tabs__header .el-tabs__item:hover {
  	padding: 0 10px;
  	color: #fff;
  	background: url(http://codegen.caihongy.cn/20221018/f6c1f39bff574b6cb2564bbedb76b604.png) no-repeat center top;
  	font-weight: 500;
  	font-size: 14px;
  	line-height: 40px;
  	position: relative;
  	text-align: center;
  	height: 40px;
  }
  
  .center-preview .el-tabs >>> .el-tabs__header .el-tabs__item.is-active {
  	padding: 0 10px;
  	color: #fff;
  	background: url(http://codegen.caihongy.cn/20221018/f6c1f39bff574b6cb2564bbedb76b604.png) no-repeat center top;
  	font-weight: 500;
  	font-size: 14px;
  	line-height: 40px;
  	position: relative;
  	text-align: center;
  	height: 40px;
  }
  
  .center-preview .el-tabs >>> .el-tabs__content .el-tab-pane {
  	padding: 10px;
  	background: #fff;
  	float: right;
  }
  
  .center-preview-pv .el-form-item >>> .el-form-item__label {
  	padding: 0 10px 0 0;
  	color: #666;
  	font-weight: 500;
  	width: 110px;
  	font-size: 14px;
  	line-height: 40px;
  	text-align: center;
  }
  
  .center-preview-pv .el-form-item .el-form-item__content {
    margin-left: 110px;
  }
  
  .center-preview-pv .el-input >>> .el-input__inner {
  	border: 0;
  	border-radius: 4px;
  	padding: 0 12px;
  	outline: none;
  	color: #000;
  	background: #BFD2FC;
  	width: 100%;
  	font-size: 14px;
  	height: 40px;
  }
  
  .center-preview-pv .el-select >>> .el-input__inner {
  	border: 0;
  	border-radius: 4px;
  	padding: 0 12px;
  	outline: none;
  	color: #000;
  	background: #BFD2FC;
  	width: 100%;
  	font-size: 14px;
  	height: 40px;
  }
  
  .center-preview-pv .el-date-editor >>> .el-input__inner {
  	border: 0;
  	border-radius: 4px;
  	padding: 0 10px 0 30px;
  	outline: none;
  	color: #000;
  	background: #BFD2FC;
  	width: 200px;
  	font-size: 14px;
  	height: 40px;
  }
  
  .center-preview-pv >>> .avatar-uploader-icon {
  	border: 1px dashed #BFD2FC;
  	cursor: pointer;
  	border-radius: 6px;
  	color: #BFD2FC;
  	width: 100px;
  	font-size: 32px;
  	line-height: 100px;
  	text-align: center;
  	height: 100px;
  }
  
  .center-preview-pv .el-form-item.balance >>> .el-input__inner {
  	border: 0;
  	border-radius: 0;
  	padding: 0 12px;
  	outline: none;
  	color: #000;
  	background: #BFD2FC;
  	width: 100%;
  	font-size: 14px;
  	height: 40px;
  }
</style>
