<script>
import Vue from 'vue'
export default {
    data() {
		return {
      activeIndex: '0',

      menuList: [
        { name: "", url: "/index/home" },
        { name: "", url: "/index/zixishi" },
        { name: "", url: "/index/yuyuexinxi" },
        { name: "", url: "/index/tousu" },
        { name: "", url: "/index/heimingdan" },
      ],

			roleMenus: [{"backMenu":[{"child":[{"appFrontIcon":"cuIcon-discover","buttons":["","","",""],"menu":"","menuJump":"","tableName":"xuesheng"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-goodsnew","buttons":["","","",""],"menu":"","menuJump":"","tableName":"zixishi"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-present","buttons":["","","",""],"menu":"","menuJump":"","tableName":"yuyuexinxi"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-send","buttons":["","","",""],"menu":"","menuJump":"","tableName":"quxiaoyuyue"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-qrcode","buttons":["","","",""],"menu":"","menuJump":"","tableName":"qiandaoxinxi"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-list","buttons":["",""],"menu":"","tableName":"config"},{"appFrontIcon":"cuIcon-news","buttons":["","","",""],"menu":"","tableName":"news"}],"menu":""}],"frontMenu":[{"child":[{"appFrontIcon":"cuIcon-discover","buttons":["",""],"menu":"","menuJump":"","tableName":"zixishi"}],"menu":""}],"hasBackLogin":"","hasBackRegister":"","hasFrontLogin":"","hasFrontRegister":"","roleName":"","tableName":"users"},{"backMenu":[{"child":[{"appFrontIcon":"cuIcon-present","buttons":["","",""],"menu":"","menuJump":"","tableName":"yuyuexinxi"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-send","buttons":[""],"menu":"","menuJump":"","tableName":"quxiaoyuyue"}],"menu":""},{"child":[{"appFrontIcon":"cuIcon-qrcode","buttons":[""],"menu":"","menuJump":"","tableName":"qiandaoxinxi"}],"menu":""}],"frontMenu":[{"child":[{"appFrontIcon":"cuIcon-discover","buttons":["",""],"menu":"","menuJump":"","tableName":"zixishi"}],"menu":""},],"hasBackLogin":"","hasBackRegister":"","hasFrontLogin":"","hasFrontRegister":"","roleName":"","tableName":"xuesheng"}],
			baseUrl: '',
			carouselList: [],
			// menuList: [],
			form: {
				ask: '',
				userid: localStorage.getItem('userid')
			},
			Token: localStorage.getItem('Token'),
            username: localStorage.getItem('username'),
            notAdmin: localStorage.getItem('sessionTable')!='"users"',
			timer: '',
			iconArr: [
				'el-icon-star-off',
				'el-icon-goods',
				'el-icon-warning',
				'el-icon-question',
				'el-icon-info',
				'el-icon-help',
				'el-icon-picture-outline-round',
				'el-icon-camera-solid',
				'el-icon-video-camera-solid',
				'el-icon-video-camera',
				'el-icon-bell',
				'el-icon-s-cooperation',
				'el-icon-s-order',
				'el-icon-s-platform',
				'el-icon-s-operation',
				'el-icon-s-promotion',
				'el-icon-s-release',
				'el-icon-s-ticket',
				'el-icon-s-management',
				'el-icon-s-open',
				'el-icon-s-shop',
				'el-icon-s-marketing',
				'el-icon-s-flag',
				'el-icon-s-comment',
				'el-icon-s-finance',
				'el-icon-s-claim',
				'el-icon-s-opportunity',
				'el-icon-s-data',
				'el-icon-s-check'
			],	
		}
    },
    created() {
		this.baseUrl = this.$config.baseUrl;
		//this.menuList = this.$config.indexNav;
      console.log("menuList", this.menuList); // 
		this.getCarousel();
    },
    mounted() {
        this.activeIndex = localStorage.getItem('keyPath') || '0';
    },
    watch: {
        $route(newValue) {
            let that = this
            let url = window.location.href
            let arr = url.split('#')
            for (let x in this.menuList) {
                if (newValue.path == this.menuList[x].url) {
                    this.activeIndex = x
                }
            }
            this.Token = localStorage.getItem('Token')
        },
    },
    methods: {
        handleSelect(keyPath) {
            if (keyPath) {
                localStorage.setItem('keyPath', keyPath)
            }
        },
		toLogin() {
		  this.$router.push('/login');
		},
        logout() {
            localStorage.clear();
            Vue.http.headers.common['Token'] = "";
            this.$router.push('/index/home');
            this.activeIndex = '0'
            localStorage.setItem('keyPath', this.activeIndex)
            this.Token = ''
            this.$forceUpdate()
            this.$message({
                message: '',
                type: 'success',
                duration: 1000,
            });
        },
		getCarousel() {
			this.$http.get('config/list', {params: { page: 1, limit: 3 }}).then(res => {
				if (res.data.code == 0) {
					this.carouselList = res.data.data.list;
				}
			});
		},
		goBackend() {
			window.open(`${this.$config.baseUrl}admin/dist/index.html`, "_blank");
		},
		goMenu(path) {
            if (!localStorage.getItem('Token')) {
                this.toLogin();
            } else {
                this.$router.push(path);
            }
		},
    }
}
</script>

